import { styled } from '../../utils/styled';
import { outlineStyle } from '../../utils/styles';
import { color, fontFamily, radius, spacing, transitionTime, typography } from '../../utils/theme';

export const Container = styled.div`
	position: relative;
	display: grid;
	grid-auto-flow: row;
	gap: ${spacing(1)};
	width: 100%;
`;

export const CheckboxInput = styled.input`
	/* First we set colors */

	/* Unchecked */
	--accent: ${color('border.primary')};
	--backdrop: ${color('background.primary')};

	/* Checked or Indeterminate */
	&:checked,
	&:indeterminate {
		--accent: ${color('interactive.primary.idle')};
		--backdrop: ${color('neutral2')};
	}

	/* Hover */
	&:hover {
		--accent: ${color('interactive.primary.hover')};
	}

	/* Pressed */
	&:active {
		--accent: ${color('interactive.primary.pressed')};
	}

	/* Error */
	&[aria-invalid='true'] {
		--accent: ${color('interactive.red.idle')};
		--backdrop: ${color('surface.red50')};
	}

	/* Disabled */
	&:disabled {
		--accent: ${color('interactive.content.disabled')};
		--backdrop: ${color('surface.secondary')};

		&:checked,
		&:indeterminate {
			--backdrop: ${color('neutral2')};
		}
	}

	/* Create a custom checkbox and hide the browser's default checkbox */

	/* Unchecked */
	display: inline-block;
	appearance: none;
	margin: 0;
	height: 14px;
	width: 14px;
	box-sizing: border-box;
	border: 1px solid var(--accent);
	background-color: var(--backdrop);
	border-radius: ${radius('sm')};
	transition: background-color ${transitionTime()}, border ${transitionTime()};

	/* Checked  */
	/* ':where' causes this selector not to gain specificity and allows other styles to override it. Without it it had more specificity than '&[aria-invalid='true']' */
	&:checked:where(:not(:indeterminate)) {
		background-color: var(--accent);
		border: none;

		&:after {
			content: '';
			position: absolute;
			display: block;
			left: 5px;
			top: 5px;
			width: 3px;
			height: 6px;
			border: solid var(--backdrop);
			border-width: 0 1px 1px 0;
			transform: rotate(45deg);
		}
	}

	/* Indeterminate  */
	&:indeterminate {
		background-color: var(--accent);
		border: none;

		&:after {
			content: '';
			position: absolute;
			display: block;
			border: solid var(--backdrop);
			border-width: 1px 0 0 0;
			top: 9px;
			left: 4px;
			width: 6px;
			height: 1px;
		}
	}

	/* Error */
	&[aria-invalid='true'] {
		border: 1px solid var(--accent);
		background-color: var(--backdrop);

		&:after {
			border-color: var(--accent);
		}
	}

	/* Focus outline is only visible when user is using keyboard. This behavior matches native checkbox behavior */
	&:focus-visible {
		${outlineStyle}
	}
`;

export const Label = styled.label`
	display: grid;
	grid-template-columns: min-content;
	grid-auto-flow: column;
	gap: ${spacing(2)};
	align-items: center;
	font-family: ${fontFamily('default')};
	margin: 0;
`;

export const Text = styled.span`
	${typography('description')};
	color: ${color('content.primary')};
	cursor: default;
`;

export const MultiCheckboxContainer = styled.div`
	width: 100%;
`;

export const MultiCheckbox = styled.div`
	display: grid;
	gap: ${spacing(3)};
`;

export const MultiCheckboxErrorContainer = styled.div`
	padding-top: ${spacing(3)};
	display: grid;
	gap: ${spacing(1)};
`;
